import { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import tigerLogo from '../images/tiger.svg'

function Header() {

  const [top, setTop] = useState(true);

  // detect whether user has scrolled the page down by 10px 
  useEffect(() => {
    const scrollHandler = () => {
      window.pageYOffset > 10 ? setTop(false) : setTop(true)
    };
    window.addEventListener('scroll', scrollHandler);
    return () => window.removeEventListener('scroll', scrollHandler);
  }, [top]);

  return (
    <header className={`fixed w-full border-b-2 border-golden z-30 md:border-opacity-90 md:bg-opacity-90 transition duration-300 ease-in-out ${!top && 'bg-golden backdrop-blur-sm shadow-lg'}`}>
      <div className="max-w-6xl mx-auto px-5 sm:px-6">
        <div className="flex items-center justify-between h-16 md:h-20">

          {/* Site branding */}
          <div className="flex-shrink-0 mr-4">
            {/* Logo */}
            <Link to="/" className="block" aria-label="Head">
              <img alt='' className="object-cover" src={tigerLogo} style={{ height: '45px', width: '45px' }} />
            </Link>
          </div>

          {/* Site navigation */}
          <nav className="flex flex-grow">
            <ul className="flex flex-grow justify-end flex-wrap items-center">
              <li>
                <Link to="about" className="font-medium text-white opacity-70 hover:opacity-90 px-5 py-3 flex items-center transition duration-150 ease-in-out">About Us</Link>
              </li>
              <li>
                <Link to="apply" className="btn-sm text-gray-800 bg-gray-100 hover:bg-gray-200 ml-3">
                  <span>Apply</span>
                  <svg className="w-3 h-3 fill-current text-gray-400 flex-shrink-0 ml-2 -mr-1" viewBox="0 0 12 12" xmlns="http://www.w3.org/2000/svg">
                    <path d="M11.707 5.293L7 .586 5.586 2l3 3H0v2h8.586l-3 3L7 11.414l4.707-4.707a1 1 0 000-1.414z" fillRule="nonzero" />
                  </svg>
                </Link>
              </li>
            </ul>

          </nav>

        </div>
      </div>
    </header>
  );
}

export default Header;
